import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class GiphyService {
    private giphyUri: string = 'http://api.giphy.com/v1/gifs/search?api_key=dc6zaTOxFJmzC&q=';

    constructor(private http: Http) { }

    attemptGiphyFetch(_queryParam: string): Promise<{response: Object}> {
        return new Promise((resolve) => {
            return this.http.get(this.buildGiphyQueryString(_queryParam)).subscribe(data => {
                let report = {};
                data = data.json();
                report['success'] = true;
                report['giphys'] = data['data'];
                resolve({ response: report });
            }, error => {
                let report = {};
                error = error.json();
                report['success'] = false;
                resolve({ response: report });
            });
        });
    }

    buildGiphyQueryString(_queryParam): string {
        if (_queryParam !== null && _queryParam !== 'undefined') {
            return this.giphyUri + _queryParam;
        }
    }
}



